///////////////////\n// Default Variables\n//////////////////////////////\n$Breakpoint-Settings: (\n  'default media': all,\n  'default feature': min-width,\n  'default pair': width,\n\n  'force all media type': false,\n  'to ems': false,\n  'transform resolutions': true,\n\n  'no queries': false,\n  'no query fallbacks': false,\n\n  'base font size': 16px,\n\n  'legacy syntax': false\n);\n\n$breakpoint: () !default;\n\n//////////////////////////////\n// Imports\n//////////////////////////////\n@import \"breakpoint/settings\";\n@import 'breakpoint/context';\n@import 'breakpoint/helpers';\n@import 'breakpoint/parsers';\n@import 'breakpoint/no-query';\n\n@import 'breakpoint/respond-to';\n\n@import \"breakpoint/legacy-settings\";\n\n//////////////////////////////\n// Breakpoint Mixin\n//////////////////////////////\n\n@mixin breakpoint($query, $no-query: false) {\n  @include legacy-settings-warning;\n\n  // Reset contexts\n  @include private-breakpoint-reset-contexts();\n\n  $breakpoint: breakpoint($query, false);\n\n  $query-string: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","\n$ff-sans:  \"futura-pt\", Futura, Verdana, sans-serif;\n\n$ff-serif: 'Butler', Georgia, serif;\n\n$ff-alt:   nth($ff-serif,2), serif;\n\n$ff-mono:  Source Code Pro, monospace;","* {\n  margin: unset;\n  padding: unset;\n  box-sizing: border-box;\n  -webkit-tap-highlight-color: transparent;\n}\n","@import \"~@/styles/theme/colors\";\n\nhr {\n  $edge: mix(Color(dark),Color(light));\n  border-style: solid;\n  border-width: 1px 0 0;\n  border-color: rgba($edge,.5);\n  margin: 1em 0;\n}","blockquote {\n  margin: 1em 0 1em .4em;\n  padding: 0 0 0 .8em;\n  border-left: 2px solid Color(theme);\n}","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-brand: #D21034;\n$c-alt:   #7D1214;\n\n$colors:(\n  dark:      $c-black, // #0E0E0E\n  slate:     lighten($c-black,15%), // #323232,\n  silver:    lighten($c-black,67%), // #B8B8B8\n  cream:     lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  creamDark: mix(#BFB4A0,$c-black,75%),\n  light:     mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:     $c-matte, // #F0F0F0\n  theme:     $c-theme, // #BA9454\n  alt:       $c-alt,\n  brand:     $c-brand,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}","@import \"~@/styles/theme/colors\";\n@import \"~@/styles/theme/fonts\";\n\ntable {\n  text-align: left;\n  border-spacing: 0;\n  border-collapse: collapse;\n  line-height: 1.6;\n  margin: 1.5em 0;\n  width: 100%;\n}\n\ncaption {\n  margin: 0 0 1em;\n  padding: .3em .5em;\n  \n  color: Color(theme);\n\n  border-style: solid;\n  border-width: 1px 0 0 4px;\n  text-align: left;\n  \n  text-transform: uppercase;\n  letter-spacing: .33em;\n}\n\nth, td {\n  padding: .5em .6em;\n  border: solid Color(theme);\n  border-width: 0;\n  vertical-align: top;\n  tr + tr & { border-top-width:  1px }\n  & + &     { border-left-width: 1px }\n}\nth {\n  font-weight: 600\n}  ","article {\n  >*:not(.UiHeading) {\n    &:first-child {\n      margin-top: 0;\n    }\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n}","@import \"~@/styles/tools/BreakClass\";\n\n.hide {\n  @include BreakClass {\n    &:not(.show_#{$_}) { display: none !important }\n  }\n}","@import \"~@/styles/theme/breaks\";\n\n// set global\n$_: '' !default;\n\n@mixin BreakClass( $mq: max-width ){\n  \n  // data\n  $screens: (\n    tablet:  4,\n    mobile:  2, \n  );\n  \n  // loop\n  @each $c, $q in $screens {\n    $_: $c !global; // update globals\n    &_#{$c} {\n      @include Break( $mq Breaks($q) ){\n        @content\n      }\n    }\n  };\n\n  // reset globals\n  $_: '' !global;\n}","@import \"~@/styles/tools/BreakClass\";\n\n.reorder {\n  &First {\n    @include BreakClass { order: -1 !important }\n  }\n  &Last {\n    @include BreakClass { order: 9999999 !important }\n  }\n}","@import \"~@/styles/base/external\";\n@import \"~@/styles/base/doc\";\n\n//=Class Systems=>\n// \n@import \"~@/styles/extend/visibility.scss\";\n@import \"~@/styles/extend/reorder.scss\";\n\n//=Staging Area=>\n//\n\na { color: inherit }\n\n.serif {\n  font-family: $ff-serif;\n}\n\n//=Module Partials=>\n//\n@import \"~@/styles/modules/clearfix.scss\";\n@import \"~@/styles/modules/textPop.scss\";\n@import \"~@/styles/modules/buttonGroup.scss\";\n@import \"~@/sty