Color(dark) calc(100% + -3rem)\n      );\n      opacity: 1;\n      transition: $transition;\n      box-sizing: content-box;\n    }\n    .UiIcon {\n      position: absolute;\n      bottom: 3.3rem;\n      left: 50%;\n      transform: translateX(-50%);\n      transition: $transition;\n      transition-delay: 0s;\n      cursor: pointer;\n      /deep/ [stroke],\n      /deep/ path {\n        transition: .38s .1s ease-out;\n        transform-origin: center;\n      }\n      /deep/ [stroke] { stroke: $icon-color !important; }\n      /deep/ path  {\n        fill: $icon-color !important;\n        stroke-width: 2px;\n        stroke: transparent;\n      }\n      &:hover {\n        /deep/ [stroke] { stroke: transparent !important; }\n        /deep/ path {\n          transform: scale(1.4);\n          stroke: $icon-color;\n        }\n      }\n    }\n    .UiBox_expanded & {\n      &:before {\n        opacity: 0;\n        pointer-events: none;\n      }\n      .UiIcon {\n        bottom: 2.3em;\n        transform: translateX(-50%) rotate(45deg);\n        transition-delay: .1s;\n      }\n    }\n  }\n  &_block {\n    display: block;\n  }\n  &_tall {\n    @include Break( min-width Breaks(4)){    \n      min-height: 65vh;\n      height: min-content;\n      max-height: 100%;\n      box-sizing: content-box;\n    }\n  }\n  &_connected {\n    $length: 8rem;\n    &:before, &:after,\n    &Top:before,\n    &Bottom:after {\n      content: \"\";\n      z-index: 2;\n      position: absolute;\n      left: 50%;\n      width: 1px;\n      height: $length;\n      background: Color(theme);\n      margin-left: -1px;\n    }\n    &, &Top {\n      padding-top: $length + 2rem;\n      &:before { top: 0 } //-1.5rem }\n    }\n    &, &Bottom {\n      padding-bottom: $length + 2rem;\n      &:after  { bottom: 0 }\n    }\n  }\n  &_overflow {\n    overflow: visible;\n  }\n  &_jam {\n    &Top, &T {\n      @include Break( min-width Breaks(4) ){\n        padding-top: 0;\n      }\n      @include BreakClass {\n        padding-top: 0;\n      }\n    }\n    &Right, &R {\n      @include Break( min-width Breaks(4) ){\n        padding-right: 0;\n      }\n      @include BreakClass {\n        padding-right: 0;\n      }\n    }\n    &Bottom, &B {\n      @include Break( min-width Breaks(4) ){\n        padding-bottom: 0;\n      }\n      @include BreakClass {\n        padding-bottom: 0;\n      }\n    }\n    &Left, &L {\n      @include Break( min-width Breaks(4) ){\n        padding-left: 0;\n      }\n      @include BreakClass {\n        padding-left: 0;\n      }\n    }\n  }\n}\n","@import \"~@/styles/theme/colors\";\n@import \"~@/styles/theme/breaks\";\n\n%UiBox {\n  & {\n    position: relative;\n\n    align-self: stretch;\n    padding: 5rem 4rem;\n    max-width: 100%;\n    overflow: hidden;\n  }\n  &_center {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    align-content: center;\n  }\n  &.wrap {\n    padding-left: 0;\n    padding-right: 0;\n  }\n  @include Break( max-width Breaks(-1)){\n    padding: 7rem 4rem;\n  }\n  @include Break( max-width Breaks(2)){\n    padding: 5rem 2.5rem;\n  }\n}\n\n%UiBox_minh {\n  min-height: 42vh;\n}\n\n%UiBox_compact {\n\n  padding: 2rem 1.5rem !important;\n  \n  @media ( max-width:800px ) {\n    padding: 2rem 1.5rem;\n  }\n  \n}\n\n.UiBox { // margin pushers\n  &_push-left {\n    margin-left:   auto;\n    margin-right:  0;\n  }\n  &_push-right {\n    margin-right:  auto;\n    margin-left:   0;\n  }\n  &_push-up {\n    margin-top:    auto;\n    margin-bottom: 0;\n  }\n  &_push-down {\n    margin-bottom: auto;\n    margin-top:    0;\n  }\n}\n\n.UiBox { // doubled\n  $B: #{&};\n  &_doubled {\n    &.UiBoxImage {//:not(.UiBox_stack) { /*<- why did I add that suffix?*/\n      flex-flow: nowrap row;\n      [style='background-image: url(\"\")'] {\n        background-color: transparent !important;\n      }\n    }\n    & > #{$B}[class*=\"UiTheme_\"] {\n      box-shadow: 0 .3em 4em -.6em rgba(0,0,0,.15);\n    }\n    &.UiBoxImage > #{$B}[class*=\"UiTheme_\"] {\n      box-shadow: 0 0 8em 0 rgba(0,0,0,.5);\n    }\n  }\n}\n","//////////////////////////////\n// Default Variables\n//////////////////////////////\n$Breakpoint-Settings: (\n  'default media': all,\n  'default feature': min-width,\n  'default pair': width,\n\n  'force all media type': false,\n  'to ems': false,\n  'transform resolutions': true,\n\n  'no queries': false,\n  'no query fallbacks': false,\n\n  'base font size': 16px,\n\n  'legacy syntax': false\n);\n\n$breakpoint: () !default;\n\n//////////////////////////////\n// Imports\n//////////////////////////////\n@import \"breakpoint/settings\";\n@import 'breakpoint/context';\n@import 'breakpoint/helpers';\n@import 'breakpoint/parsers';\n@import 'breakpoint/no-query';\n\n@import 'breakpoint/respond-to';\n\n@import \"breakpoint/legacy-settings\";\n\n//////////////////////////////\n// Breakpoint Mixin\n//////////////////////////////\n\n@mixin breakpoint($query, $no-query: false) {\n  @include legacy-settings-warning;\n\n  // Reset contexts\n  @include private-breakpoint-reset-contexts();\n\n  $breakpoint: breakpoint($query, false);\n\n  $query-string: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-brand: #D21034;\n$c-alt:   #7D1214;\n\n$colors:(\n  dark:      $c-black, // #0E0E0E\n  slate:     lighten($c-black,15%), // #323232,\n  silver:    lighten($c-black,67%), // #B8B8B8\n  cream:     lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  creamDark: mix(#BFB4A0,$c-black,75%),\n  light:     mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:     $c-matte, // #F0F0F0\n  theme:     $c-theme, // #BA9454\n  alt:       $c-alt,\n  brand:     $c-brand,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}","@import \"~@/styles/theme/breaks\";\n\n// set global\n$_: '' !default;\n\n@mixin BreakClass( $mq: max-width ){\n  \n  // data\n  $screens: (\n    tablet:  4,\n    mobile:  2, \n  );\n  \n  // loop\n  @each $c, $q in $screens {\n    $_: $c !global; // update globals\n    &_#{$c} {\n      @include Break( $mq Breaks($q) ){\n        @content\n      }\n    }\n  };\n\n  // reset globals\n  $_: '' !global;\n}"],"sourceRoot":""}]);

