\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import '~@/styles/theme/fonts';\n@import '~@/styles/theme/breaks';\n@import '~@/styles/theme/colors';\n\n%blockLink {\n  display: inline-flex;\n  justify-content: center;\n  align-items: center;\n  color: Color(light);\n  padding: .25em 1.5em;\n  font-weight: normal !important;\n  text-decoration: none;\n  & + * {\n    margin-top: .5rem;\n  }\n  .UiIcon:first-child {\n    margin-right: .5em;\n  }\n}\n\n.AppMenu {\n\n  $B: &;\n\n  & {\n    width: 100vw;\n    height: 100%;\n    overflow: scroll;\n    -webkit-overflow-scrolling: touch;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    position: fixed;\n    display: flex;\n    flex-flow: nowrap column;\n    padding: 2rem 0 0;\n    justify-content: flex-start;\n    align-items: center;\n    &:not(#{$B}_open) {\n      display: none !important;\n    }\n  }\n    \n  &--close {\n    position: absolute;\n    top: 2rem;\n    right: 2rem;\n    transform: rotate(45deg);\n  }\n  &--list {\n    margin: auto;\n    list-style: none;\n    // border-right: 1px solid;\n    &-item {\n      display: flex;\n      position: relative;\n      & + & {\n        // border-top: 1px solid;\n      }\n      > ul {\n        display: flex;\n        list-style: none;\n        // position: absolute;\n        // left: 100%;\n        // top: 0;\n        margin: 0;\n        white-space: nowrap;\n        &, > * { transition: .3s 0s ease }\n        &:before,\n        &:after {\n          content: '';\n          position: absolute;\n          top: 50%;\n          margin-top: -8px;\n          width: 0;\n          height: 0;\n          color: Color(silver);\n          border-top: 8px solid transparent;\n          border-bottom: 8px solid transparent;\n          border-left: 8px solid currentColor;\n          transition: .3s 0s ease;\n          // opacity: 0;\n        }\n        &:after {\n          color: Color(light);\n          margin-left: -1.25px;\n          z-index: 9999;\n        }\n      }\n      // NO HOVER FX\n      // &:not(:hover) > ul {\n      //   opacity: 0;\n      //   pointer-events: none;\n      //   > * { transform: translateX(-1rem) }\n      // }\n      & > ul {\n        z-index: 2;\n        color: Color(silver);\n        > * {\n          font-size: .8em;\n          font-weight: 400;\n          text-transform: uppercase;\n          letter-spacing: .1em;\n          text-decoration: underline transparent;\n          &:hover {\n            text-decoration-color: Color(theme) !important;\n          }\n        }\n      }\n      &:hover > ul,\n      > ul:hover {\n        opacity: 1;\n        pointer-events: initial;\n        &:before, &:after {\n          opacity: 1;\n        }\n        color: Color(theme);\n      }\n    }\n    &-link {\n      display: inline-flex;\n      align-items: center;\n      padding: 0.5rem 1rem;\n      text-align: right;\n      cursor: pointer;\n      transition: .3s 0s ease;\n      &[href]:hover {\n        color: Color(theme);\n      }\n      &_social {\n        padding-top: 1.2rem;\n        padding-bottom: 1.2rem;\n        /deep/ > * {\n          transition: .2s 0s ease;\n          &:hover * {\n            fill: Color(theme);\n          }\n        }\n      }\n    }\n    &-item > &-link {\n      width: 8rem;\n      white-space: nowrap;\n      direction: rtl;\n      border-right: 1px solid Color(silver);\n      font-family: $ff-serif;\n      text-transform: uppercase;\n      letter-spacing: .1em !important;\n    }\n    &-item:not(:first-child) > &-link {\n      border-top: 1px solid Color(silver);\n    }\n    @include Break( max-width Breaks(3) ){\n      & {\n        flex: 1 0 25em;\n        display: flex;\n        flex-flow: nowrap column;\n        justify-content: stretch;\n        align-items: stretch;\n        margin: auto auto auto 1.5rem !important;\n        &-item {\n          flex: 1;\n        }\n        &-link {\n          width: unset !important;\n          flex: 1;\n        }\n        &-item > ul {\n          transition: .3s ease;\n          position: absolute;\n          left: 100%;\n          top: 50%;\n          transform: translateY(-50%);\n          flex-flow: nowrap column;\n          justify-content: flex-start;\n          line-height: 1.1;\n        }\n      }\n    }\n  }\n  &--topStrip {\n    position: relative;\n    display: flex;\n    justify-content: space-between;\n    align-items: flex-end;\n    width: 100vw;\n    padding: 0 0 0 1.5rem;\n    @include Break( min-width Breaks(3) ){\n      flex: 0 0 5rem;\n    }\n    @include Break( max-width Breaks(3) ){\n      min-height: 9rem;\n      margin-bottom: 2rem;\n    }\n  }\n  &--brand {\n    // position: absolute;\n    // top: 1.5rem;\n    // left: 1.5rem;\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    .UiIcon {\n      max-width: 100%;\n      margin-right: 2rem;\n    }\n    &-slug {\n      display: block;\n      line-height: 1.3;\n      font-size: .8rem;\n      color: Color(theme);\n    }\n    @include Break( max-width Breaks(3) ){\n      flex-flow: nowrap column;\n      text-align: center;\n      .UiIcon {\n        margin: 0 auto 1rem;\n      }\n    }    \n  }\n  &--linkBlock {\n    text-transform: uppercase;\n    letter-spacing: .075em;\n    & {\n      display: flex;\n      flex-flow: nowrap column;\n      max-width: 15rem;\n      font-size: 0.8rem;\n      @include Break( min-width Breaks(3) ){\n        position: absolute;\n        top: 100%;\n        right: 0;\n      }\n    }\n    > * {\n      @extend %blockLink;\n    }\n  }\n\n  &--footer {\n    flex: 0 0 6rem;\n    display: flex;\n    flex-flow: nowrap column;\n    width: 100%;\n    margin: 2rem 0 0;\n    &-links {\n      display: none;\n      width: 100%;\n      align-items: center;\n      > * {\n        @extend %blockLink;\n        flex: 1;\n        margin: 0;\n      }\n    }\n    @include Break( max-width Breaks(3) ){\n      flex: 0 9rem;\n      min-height: 9rem;\n      &-links {\n        display: flex;\n        margin-top: 0;\n      }\n      .WineSearch {\n        flex: 1;\n      }\n      @at-root .AppMenu--topStrip {\n        display: none !important;\n      }\n      @at-root\n      .AppMenu--list {\n        flex: 1;\n        margin-top: auto !important;\n        margin-bottom: auto !important;\n        min-height: 25em;\n        max-height: 38em;\n      }\n    }\n  }\n  \n  &--list-link { &.active, &.open {\n    font-weight: bold;\n    letter-spacing: 0 !important;\n    text-transform: capitalize;\n    color: Color(theme) !important;\n    font-style: italic;\n  } }\n  &--subList &--list-link { &.active, &.open {\n    font-style: normal;\n    text-transform: uppercase;\n  } }\n}\n","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-brand: #D21034;\n$c-alt:   #7D1214;\n\n$colors:(\n  dark:      $c-black, // #0E0E0E\n  slate:     lighten($c-black,15%), // #323232,\n  silver:    lighten($c-black,67%), // #B8B8B8\n  cream:     lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  creamDark: mix(#BFB4A0,$c-black,75%),\n  light:     mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:     $c-matte, // #F0F0F0\n  theme:     $c-theme, // #BA9454\n  alt:       $c-alt,\n  brand:     $c-brand,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}","\n$ff-sans:  \"futura-pt\", Futura, Verdana, sans-serif;\n\n$ff-serif: 'Butler', Georgia, serif;\n\n$ff-alt:   nth($ff-serif,2), serif;\n\n$ff-mono:  Source Code Pro, monospace;","//////////////////////////////\n// Default Variables\n//////////////////////////////\n$Breakpoint-Settings: (\n  'default media': all,\n  'default feature': min-width,\n  'default pair': width,\n\n  'force all media type': false,\n  'to ems': false,\n  'transform resolutions': true,\n\n  'no queries': false,\n  'no query fallbacks': false,\n\n  'base font size': 16px,\n\n  'legacy syntax': false\n);\n\n$breakpoint: () !default;\n\n//////////////////////////////\n// Imports\n//////////////////////////////\n@import \"breakpoint/settings\";\n@import 'breakpoint/context';\n@import 'breakpoint/helpers';\n@import 'breakpoint/parsers';\n@import 'breakpoint/no-query';\n\n@import 'breakpoint/respond-to';\n\n@import \"breakpoint/legacy-settings\";\n\n//////////////////////////////\n// Breakpoint Mixin\n//////////////////////////////\n\n@mixin breakpoint($query, $no-query: false) {\n  @include legacy-settings-warning;\n\n  // Reset contexts\n  @include private-breakpoint-reset-contexts();\n\n  $breakpoint: breakpoint($query, false);\n\n  $query-string: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n"],"sourceRoot":""}]);

